import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Escalonamento_formulario } from '../../../modelo/Escalonamento_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'escalonamento_formulario-dados-details',   templateUrl: 'escalonamento_formulario_dadosDetails.component.html' }) export class Escalonamento_formularioDadosDetailsComponent implements OnInit {    escalonamento_formulario: Escalonamento_formulario;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.escalonamento_formulario.Escalonamento_formulario";                 let id = +params['id'];         this.escalonamento_formulario = new Escalonamento_formulario();         this.escalonamento_formulario.cd_escalonamento_formulario = id;         o.objetoJson = this.escalonamento_formulario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a escalonamento_formulario. '; }      this.escalonamento_formulario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


